import { Provider } from "react-redux";
import { store } from "./store";

interface IProps {
  children?: JSX.Element;
}

/**
 * ,   redux   .
 * @param props   
 * @returns react 
 */
function ReduxProvider(props: IProps) {
  return (
    <>
      <Provider store={store}>{props.children}</Provider>
    </>
  );
}

export default ReduxProvider;
